* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

html {
  background: rgb(203, 229, 241);
  min-height: 720px;
  min-width: 320px;
}

html,
body {
  height: 100%;
}

#root {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

.SignUp {
  background: #003b70;
  border-radius: 20px;
  box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.5);
  max-width: 615px;
  padding-bottom: 66px;
  text-align: center;
  width: 30%;
}
.SignUp > .logo {
  max-width: 90%;
}
.SignUp > .form {
  margin-bottom: 10px;
  margin-top: 11px;
}

.input-group {
  margin-bottom: 26px;
}
.input-group:nth-last-child(-n + 2) {
  margin-bottom: 60px;
}
.input-group > label,
.input-group > input {
  font-family: "Roboto", sans-serif;
  max-width: 420px;
  text-align: left;
  width: 80%;
}
.input-group > label {
  color: #ffffff;
  display: inline-block;
  margin-bottom: 5px;
  /* text-transform: uppercase; */
}
.input-group > input {
  background: #ffffff;
  border-radius: 4px;
  border: 1px solid #e5e5e5;
  color: #676767;
  font-size: 16px;
  height: 40px;
  outline: 0;
  padding: 0 15px;
  transition: all 0.3s;
}
.input-group > input:focus {
  background: #fff;
  border-color: #c4c4c4;
}
.input-group > input::placeholder {
  color: #c4c4c4;
  font-size: 16px;
}

button {
  border-radius: 5px;
  border: none;
  color: #fff;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
  max-width: 420px;
  outline: 0;
  padding: 10px 0;
  transition: background 0.3s;
  width: 80%;
}
button.primary {
  background: #0e6d8a;
}
button.primary:hover {
  background: #5ac5d3;
}
button.secondary {
  background: #8d8d8d;
}
button.secondary:hover {
  background: #5ac5d3;
}
